import React from 'react'
import Link from 'gatsby-link'
import styled from "styled-components"
import breakpoint from 'styled-components-breakpoint'
import {PhotoWrapper, TextWrapper, PageHeader, SectionHeader, Section} from "../utils/styled-components.js"

import SideMenu from '../components/SideMenu.js'

const PhotoWrapperEducation = PhotoWrapper.extend`
  background-image:   linear-gradient(rgba(0,0,0,0.45), rgba(0,0,0,0.45)),
  url('http://res.cloudinary.com/ds0f4qyyt/image/upload/s--ST9NKaaB--/c_scale,q_jpegmini,w_3525/v1493834338/smiles1_falmrt.jpg');
`
const EducationWrapper = styled.div`
  width: 100%
  height: 100%;
`
const TextWrapperEducation = TextWrapper.extend`
  ${props => breakpoint('md', props.theme.breakpoints)`
    margin-left: 10rem;
  `};
`
const MenuWrapper = styled.div`
  margin: 0 auto;
  max-width: 960px;
  display: flex;
`
// const PageHeader = styled.h1`
//   color: ${props => props.theme.color.l};
//   margin: 0 auto;
//   text-align: center;
//   margin-bottom: 2rem;
// `

export default ({data}) => {
  console.log(data)
  return(
    <EducationWrapper>
      <PhotoWrapperEducation/>
      <PageHeader>Education</PageHeader>
      <MenuWrapper id="eduwrapper">
        <SideMenu data={data.allMarkdownRemark.edges} />
        <TextWrapperEducation>
            {
              data.allMarkdownRemark.edges.map(({node}, i)=>(
                <Section
                id={node.frontmatter.title.split(' ').join('-')}
                key={i}>
                  <SectionHeader>
                {node.frontmatter.title}
                </SectionHeader>
                  <div dangerouslySetInnerHTML={{__html: node.html}}></div>
                </Section>
              ))
            }
        </TextWrapperEducation>
      </MenuWrapper>
    </EducationWrapper>
  )
}


export const query = graphql`
  query EducationQuery {
    allMarkdownRemark(
      sort: {fields: [frontmatter___order], order:ASC},
      filter: {fileAbsolutePath: {regex: "/\/home\/bobby\/projects\/Practice\/nslb-gatsby\/src\/data\/education/"}}
    ) {
      edges {
        node {
          frontmatter{
            title
          }
          html
      }
    }
  }
}
`



