import React from 'react'
import styled from "styled-components"
import breakpoint from 'styled-components-breakpoint'

import theme from "../utils/theme.js"

const Menu = styled.nav`
  position: fixed;
  top: ${props => props.theme.dimensions.headerHeight + props.theme.dimensions.pageHeaderHeight};
  display: none;
  border-radius: .125rem;
  background-color: ${props => props.theme.color.p};
  max-width: 10rem;
  ${props => breakpoint('md', props.theme.breakpoints)`
    display: block;
  `}
`
const Navlink = styled.a`
  color: white;
  display: block;
  text-decoration: none;
  padding: .5rem .5rem;
  width: 100%;
  &: hover{
    background-color: white;
    color: ${props => props.theme.color.p};
    transition: ${props => props.theme.effects.transition};
  }
`

class SideMenu extends React.Component{
  constructor(props){
    super(props)
  }
  // componentDidMount(){
  //   window.addEventListener('scroll', this.handleScroll);
  // }
  // handleScroll(){
  //   var node = ReactDOM.findDOMNode();
  //   var scrollPos = node.scrollTop;
  //   console.log(scrollPos)
  // }
  render(){
    return(
      <Menu>
        {
          this.props.data.map(({node}, i)=>(
              <Navlink
                key={i}
                href={"#" + node.frontmatter.title.split(' ').join('-')}>{node.frontmatter.title}
              </Navlink>
          ))
        }
      </Menu>
    )
  }
}

const SideNav = ({data}) => (
  <Menu>
    {
      data.map(({node}, i)=>(
          <Navlink
            key={i}
            href={"#" + node.frontmatter.title.split(' ').join('-')}>{node.frontmatter.title}
          </Navlink>
      ))
    }
  </Menu>
)

export default SideMenu



